export default (function (localName, log) {
  var callbacks = [];
  var destroyed = false;
  return {
    destroy: function destroy(error) {
      if (!destroyed) {
        destroyed = true;
        log("".concat(localName, ": Destroying connection"));
        callbacks.forEach(function (callback) {
          callback(error);
        });
      }
    },
    onDestroy: function onDestroy(callback) {
      destroyed ? callback() : callbacks.push(callback);
    }
  };
});